@import url("https://fonts.googleapis.com/css2?family=Yellowtail&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");

* {
  font-family: "Poppins";
  color: #2f2e41;
}

.brandFont {
  font-family: "Yellowtail";
  font-size: 36px !important;
}
.bg-green {
  background-color: #1d741b;
}

.btn-success {
  background-color: #1d741b !important;
}

.btn-success strong {
  color: white !important;
}

.btn-success:hover {
  background-color: #88ca5e !important;
  border-color: #88ca5e !important;
  color: #2f2e41 !important;
}

.bg-limegreen {
  background-color: #88ca5e;
}

.bg-rosequartz {
  background-color: #f1c0b9;
}

.bg-lighterrose {
  background-color: #f1dcd9;
}

.bg-neongreen {
  background-color: #d2fba4;
}

.bg-lightergreen {
  background-color: #f3fbe9 !important;
}

.homecontent {
  min-height: 80vh;
}

.fullpage {
  min-height: calc(100vh - 80px);
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.melonborder {
  outline: 5px solid #1d741b !important;
  border: 12px solid #88ca5e !important;
  padding: 16px;
}

.jumbotron {
  text-align: center;
  padding: 24px 24px;
}

.collapse {
  justify-content: flex-end;
}

.logincard {
  min-height: 286px;
  min-width: 255px !important;
}

.overlayrow {
  z-index: 1;
  position: relative;
}

.toprow {
  z-index: 2;
  position: sticky;
  top: 20vh;
  left: 0;
}

.containetOverlap {
  position: relative !important;
}

.createusercard {
  padding: 16px;
  background-color: transparent !important;
  border: none !important;
  width: 100%;
  height: 100%;
}

.createusercard form {
  width: 100%;
  height: 100%;
}

.createusercard form label {
  font-weight: 700;
  font-size: 18px !important;
}

h3 {
  margin-top: 16px !important;
  font-weight: 900 !important;
  font-family: "Yellowtail";
  font-size: 36px !important;
}
.nav-link {
  border-radius: 30px;
}

.searchbar {
  border-radius: 30px !important;
}

.listing {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.resourcecard {
  box-shadow: 2px 1px 4px 1px #6a8572;
  max-width: 100% !important;
}

.resourcecardLink {
  text-decoration: none !important;
  max-width: 100% !important;
  margin: 16px 16px;
}

@media (max-width: 480px) {
  .resourcecardLink {
    margin: 16px 0px;
  }
}

.listingContainer {
  max-width: 80vw;
  border-radius: 50px;
}
